*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 20px;
  padding: 0;
  font-family: sans-serif;
  background: #f4f4f4;
}

.clear {
  clear: both;
  content: "";
}

.game {
  margin: 0 auto;
  padding: 20px;
}
.game .game-info {
  background: #19a0d9;
  padding: 7px;
  text-align: center;
  color: #fff;
  min-height: 100px;
  border-radius: 7px;
  max-width: 500px;
  margin: 0 auto 20px;
}
.game .game-info .info {
  display: block;
  margin-top: 15px;
}
.game .cell {
  background: #7b7b7b;
  border: 1px solid #fff;
  line-height: 45px;
  height: 45px;
  text-align: center;
  width: 45px;
  cursor: pointer;
  border-radius: 5px;
  color: #fff;
  font-weight: 600;
  display: inline-block;
  vertical-align: text-bottom;
}
.game .cell:focus {
  outline: none;
}
.game .hidden {
  background: #2e2829;
}
.game .is-flag,
.game .is-mine {
  color: #fc543c;
}

.clearfix {
  text-align: center;
  white-space: nowrap;
}
.clearfix::after {
  display: block;
  content: "";
  clear: both;
}

.game button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  background-color: white;
  border: 0;
  padding: 10px 15px;
  color: #333;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  font-size: 18px;
  transition-duration: .25s;
  margin: 0 auto 20px;
  display: block;
}

.messagebox {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0, 0.7);
}
.messagebox .box {
  min-width: 200px;
  max-width: 400px;
  border-radius: 10px;
  background: #ffffff;
  overflow: hidden;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
}
.messagebox .box .body {
  padding: 15px 25px;
}
.messagebox .box .buttons {
  display: flex;
  justify-content: center;
  border-top: 1px solid #e8e8e8;
}
.messagebox .box .buttons button {
  flex: 1;
  background: #ffffff;
  border: none;
  padding: 20px;
  outline: none;
  color: #2ad181;
  text-align: center;
  font-size: 15px;
  transition: background .4s ease-in-out;
}
.messagebox .box .buttons button:hover, .messagebox .box .buttons button:active {
  background: #f7f7f7;
  cursor: pointer;
}
.messagebox .box .buttons button:active {
  box-shadow: 0 0 5px 0 #e8e8e8 inset;
}